import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { IfakeApi } from '../viewModels/Ifake-api';
import { IfakeData } from '../viewModels/ifake-data';

@Injectable({
  providedIn: 'root'
})
export class FakeApiService {

  constructor(private http:HttpClient) { }
  getAllProducts(): Observable <IfakeApi>
  {
    return this.http.get<IfakeApi>(`https://reqres.in/api/product`);
  }
}
