import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { IProduct } from '../viewModels/iproduct';
// import { IProduct } from '../ViewModels/iproduct';

@Injectable({
  providedIn: 'root'
})
export class ProductsAPIService {

  private ProductList: IProduct[]=[];

  constructor(private http: HttpClient) { }

  getAllProducts(): Observable <IProduct[]>
  {
    //return array of product
   // http://localhost:3000/products
    return this.http.get<IProduct[]>(`${environment.ApiURL}/products`);
  }

  getProductByCatID (catID:number):Observable<IProduct[]>
  {
    return this.http.get<IProduct[]>(`${environment.ApiURL}/products?CategoryId=${catID}`)
  }

  getProductByID(pID: number): Observable <IProduct>
  {
    console.log(pID + " here is api service");
    return this.http.get<IProduct>(`${environment.ApiURL}/products/${pID}`)
    //return this.http.get<IProduct>(`${environment.ApiURL}/products/${pID}`);
  }

  addProduct(prd: IProduct):Observable<IProduct>
  {
    const httpOptions = {headers: new HttpHeaders({
      'Content-Type': 'application/json'
      //,'Accept':' */*'
      //,'Authorization': 'my-auth-token'
        })};
      
    return this.http.post<IProduct>(`${environment.ApiURL}/products`,prd, httpOptions);
  }


  
}
