import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { ProductsAPIService } from 'src/app/services/products-api.service';
import { StaticProductsService } from 'src/app/services/static-products.service';
import { IProduct } from 'src/app/viewModels/iproduct';
import { OrderList } from 'src/app/viewModels/order-list';


@Component({
  selector: 'app-product2',
  templateUrl: './product2.component.html',
  styleUrls: ['./product2.component.scss']
})
export class Product2Component implements OnInit,OnChanges {
  ProductList: IProduct[]=[];
  subscribtion: Subscription|null=null;

  newProduct: IProduct= {id:13,
                         Name: 'dress', 
                         Price: 1000,
                         Quantity:40,
                         CategoryId:3
                      }
  constructor(private prdService: ProductsAPIService
    , private router:Router) { }
  ngOnChanges(changes: SimpleChanges): void {
    throw new Error('Method not implemented.');
  }

  ngOnInit(): void {
    this.subscribtion= this.prdService.getAllProducts().subscribe(
          (response)=>{
            console.log("in subscribe");
            this.ProductList=response;
          },
          (err)=>{console.log(err)}
        );
    
        console.log("After subscribe")
  }

  addProduct()
  {
    this.prdService.addProduct(this.newProduct).subscribe(
      (res)=>{
        console.log(res);
        this.router.navigate(['/Products2']);
        console.log("new product added")
      },
      (err)=>{console.log(err)}
    );
  }

  ngOnDestroy(): void {
    this.subscribtion?.unsubscribe();
  }

}















