import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'creditCard'
})
export class CreditCardPipe implements PipeTransform {

  transform(x:string): string {
    var dis=[];
    var res=x;

    for(let i =0;i<x.length; i+=4){
      dis.push(x.substring(i,i+4));
      //this.cc=dis.join("-");
    }
    if (x.length){
       res=dis.join("-");
    }
   return res;
   }
  }


