import { Component, OnInit } from '@angular/core';
import { FakeApiService } from 'src/app/services/fake-api.service';
import { IfakeApi } from 'src/app/viewModels/Ifake-api';
import { IfakeData } from 'src/app/viewModels/ifake-data';

@Component({
  selector: 'app-fake-api',
  templateUrl: './fake-api.component.html',
  styleUrls: ['./fake-api.component.scss']
})
export class FakeApiComponent implements OnInit {
fakeList:IfakeData[]=[];
//fake:IfakeApi|undefined=undefined;
//fake:any;
  constructor(private fakeService:FakeApiService) { }

  ngOnInit(): void {
    this.fakeService.getAllProducts().subscribe(
      (res)=>{
       // this.fake=res;
        this.fakeList=res.data;
      },
      (err)=>{console.log("fake api error "+err)}
    )
    
  }

}
