import { Component, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { Router } from '@angular/router';
import { CategoryApiService } from 'src/app/services/category-api.service';
import { ProductsAPIService } from 'src/app/services/products-api.service';
import { IApi } from 'src/app/viewModels/iapi';
import { ICategory } from 'src/app/viewModels/icategory';

@Component({
  selector: 'app-add-products',
  templateUrl: './add-products.component.html',
  styleUrls: ['./add-products.component.scss']
})
export class AddProductsComponent implements OnInit,OnChanges {
name:string="";
id:number=0;
quantity:number=0;
cat:ICategory[]=[];
img:string='';
price:number=0;
catID:number=0;
newProduct:IApi|undefined;
  constructor(private prdService: ProductsAPIService,private router:Router,
              private catService:CategoryApiService) {
    // this.cat=[{ID:1,Name:"Mobiles"},
    // {ID:2,Name:"clothing"},
    // {ID:3,Name:"laptops"}    
//]



   }
  ngOnChanges(changes: SimpleChanges): void {
    console.log(this.id);
    console.log(this.name);
    console.log(this.catID);
  }

  ngOnInit(): void {
    this.catService.getAllCategories().subscribe(
      (res)=>{
        this.cat=res;
        console.log("my categories "+this.cat);
      },
      (err)=>{console.log(err)}
    )

  }
  addProduct()
  {
    this.newProduct={id:this.id,Name:this.name,Quantity:this.quantity,Price:this.price,Img:this.img,CategoryId:this.catID}
    this.prdService.addProduct(this.newProduct).subscribe(
      (res)=>{
        console.log(res);
        this.router.navigate(['/Products2']);
        console.log("new product added from add products")
      },
      (err)=>{console.log(err)}
    );
  }
}
