import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddAdminComponent } from './components/add-admin/add-admin.component';
import { AddProductsComponent } from './components/add-products/add-products.component';
import { AdminComponent } from './components/admin/admin.component';
import { FakeApiComponent } from './components/fake-api/fake-api.component';
import { NotFoundComponent } from './components/not-found/not-found.component';
import { ProductDetailsComponent } from './components/product-details/product-details.component';
import { Product2Component } from './components/product2/product2.component';
import { ProductsComponent } from './components/products/products.component';
import { ShoppingCartComponent } from './components/shopping-cart/shopping-cart.component';

const routes: Routes = [

  {path: 'Products', component: ProductsComponent},
  {path: 'Order', component: ShoppingCartComponent},
  {path:'Products2',component:Product2Component},
  {path:'addProducts',component:AddProductsComponent},
  {path:'admin',component:AdminComponent},
  {path:'admin/insert',component:AddAdminComponent},
  {path:'fakeApi',component:FakeApiComponent},
 // {path:'productDetails',component:ProductDetailsComponent},
   {path: 'productDetails/:pID', component: ProductDetailsComponent},
   {path:'', redirectTo:'/Products', pathMatch:'full'}, // Default path
   {path:"**", component:NotFoundComponent} // Wildcard path
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
