import { Pipe, PipeTransform,ElementRef } from '@angular/core';

@Pipe({
  name: 'nationalID'
})
export class NationalIDPipe implements PipeTransform {

  constructor(private e:ElementRef){


  }
  transform(id:string,x:string="FullDate"): string {
   var m:string="";
   var d:string="";
   var y:string="";
    //var st:string;
    if(x=="MM"){
     
    m= id.substring(3,5);
    console.log(m)
    return m;
    }
    else if(x=="DD"){
      d= id.substring(5,7);
      return d;
    }
    else if (x=="YY"){
      console.log("yyear")
      if(id.substring(0,1)=="3"){
        console.log("stop")
        y="19"+id.substring(1,3);
      }
      else{
        y="20"+id.substring(1,3);
      }
        //y=id.substring(1,3);
        return y;
    }
    else if (x=="FullDate"){
      if(id.substring(0,1)=="3"){
  
       return id.substring(5,7)+"/"+id.substring(3,5)+"/"+"19"+id.substring(1,3);
      }
      else{
        return id.substring(5,7)+"/"+id.substring(3,5)+"/"+"20"+id.substring(1,3);
      }
    }
    return"your Full date is: "+d+"/"+m+"/"+y;
  }

}
